import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FrontpageComponent } from './frontpage/frontpage.component';
import { PizzaComponent } from './pizza/pizza.component';
import { BurgerComponent } from './burger/burger.component';
import { SalodComponent } from './salod/salod.component';
import { TacosComponent } from './tacos/tacos.component';
import { WrapsComponent } from './wraps/wraps.component';
import { FriesComponent } from './fries/fries.component';
import { DrinksComponent } from './drinks/drinks.component';

const routes: Routes = [
  { path: '', redirectTo: '/frontpage', pathMatch: 'full' },
  { path: 'frontpage', component: FrontpageComponent },
  { path: 'pizza', component: PizzaComponent  },
  { path: 'burger', component: BurgerComponent },
  { path: 'salod', component: SalodComponent },
  { path: 'tacos', component: TacosComponent },
  { path: 'wraps', component: WrapsComponent },
  { path: 'fries', component: FriesComponent },
  { path: 'drinks', component: DrinksComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
